// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { uqStringify } from "tonwa-uq";
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { FieldItem, FieldItemNumber, FieldItemString, FieldItemId, FieldItemInt, UI, TFunc } from 'tonwa-react';
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { Res, setRes } from "tonwa-core";
import { Draft } from "./BzWorkshop";

/*--fields--*/
const fields = {
	id: {
		"name": "id",
		"type": "id",
		"isKey": false,
		"label": "Id"
	} as FieldItemId,
	entity: {
		"name": "entity",
		"type": "integer",
		"isKey": false,
		"widget": "updown",
		"label": "Entity"
	} as FieldItemInt,
	content: {
		"name": "content",
		"isKey": false,
		"label": "Content"
	} as undefined,
};
/*==fields==*/

const fieldArr: FieldItem[] = [
	fields.entity, fields.content, 
];

export const ui: UI = {
label: "Draft",
fieldArr,
fields,
};

const resRaw: Res<any> = {
$zh: {
},
$en: {
}
};
const res: any = {};
setRes(res, resRaw);

export const t:TFunc = (str:string|JSX.Element): string|JSX.Element => {
return res[str as string] ?? str;
}

export function render(item: Draft):JSX.Element {
return <>{uqStringify(item)}</>;
};
